<template>
    <router-view v-if="isRouterAlive" />
</template>
<script>
import { provideI18n } from "./i18nPlugin";
export default {
    data() {
        return { isRouterAlive: true };
    },
    provide() {
        // 向后代注入刷新函数
        return {
            reload: this.reload
        };
    },
    setup() {
        var i = provideI18n({
            locale: window.localStorage.getItem("language") || "jp",
            messages: {
                en: require("./i18nPlugin/en.js"),
                cn: require("./i18nPlugin/cn.js"),
                jp: require("./i18nPlugin/jp.js")
            }
        });
        console.log(i);
    },
    methods: {
        // 刷新页面
        reload() {
            this.isRouterAlive = false;
            this.$nextTick(function() {
                this.isRouterAlive = true;
            });
        }
    },
    mounted() {
        // 检测设备将是否为移动端储存进store
        this.$store.commit("changeView")
    }
};
</script>
