
import {
  createRouter,
  createWebHistory
} from 'vue-router'

const routes = [{
    path: '/',
    name: 'top',
    component: () => import('../views/page/index.vue'),
    redirect: '/home',
    children: [{
        path: 'home',
        name: 'home',
        component: () => import('../views/page/home/index.vue')
      },
      {
        path: 'reception',
        name: 'reception',
        component: () => import('../views/page/reception/index.vue')
      },
      {
        path: 'agreement',
        name: 'agreement',
        component: () => import('../views/page/agreement/index.vue')
      }
    ],
  },
  {
    path: '/mobile',
    name: 'mobile',
    component: () => import('../views/mobile/index.vue'),
    children: [{
        path: 'register',
        name: 'register',
        component: () => import('../views/mobile/register/index.vue')
      },
      {
        path: 'forget',
        name: 'forget',
        component: () => import('../views/mobile/forget/index.vue')
      },
      {
        path: 'success',
        name: 'success',
        component: () => import('../views/mobile/success/index.vue')
      }
    ]
  },
  {
    path: '/scancode',
    name: 'scancode',
    component: () => import('../views/scancode/index.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})
router.beforeEach((to, from, next) => {
  document.documentElement.scrollTop = 0
  next()
})
export default router