import {
  createStore
} from 'vuex'
import {
  listenView
} from '../util'

export default createStore({
  state: {
    viewListen: false,
    oldListen:false
  },
  mutations: {
    changeView(state) {
      // viewListen false为非移动端，true则是移动端
      listenView().then(res => {
          console.log(res, "移动端");
          state.viewListen = true
        })
        .catch(err => {
          console.log(err, "pc端");
          state.viewListen = false
        });
    },
    switchPc(state){
      state.viewListen=false
    },
    switchMobile(state){
      state.viewListen=true
    },
    cover(state){
      state.oldListen=state.viewListen
    }
  },
  actions: {},
  modules: {}
})